.profile-page {
    position: relative;
    height: 100%;
    background: white;
    overflow: hidden;
}
    .profile-page.banner-mode{
        height: 58px;
        /* animation: profile-page-change 2000ms infinite; */
    }

    @keyframes profile-page-change {
        0% { height: 100%; }
        100% { height: 58px; }
    }

.profile-body {
    position: absolute;
    width: 100%;
    bottom: 0;
    overflow: hidden;
}
    .profile-body.banner-mode{
        /* animation: profile-body-change; */
        top: 0;
        bottom: auto !important;
        background: black;
    }

    @keyframes profile-body-change {
        0% {
            top: calc(100% - 56px);
            bottom: 0 !important;
            background: transparent;
        }
        100% {
            top: 0;
            bottom: calc(100% - 56px) !important;
            background: black;
        }
    }

.profile-pic > img {
    width: 100%;
    margin-bottom: 50
}
    .profile-pic.banner-mode {
        /* animation: profile-pic-change; */
        overflow: hidden;
        width: 50px;
        height: 50px;
        border: 2px solid white;
        border-radius: 100%;
        margin: 4px;
    }
    .profile-pic.banner-mode > img {
        /* animation: profile-pic-image-change; */
        width: 50px;
        margin-right: -5px;
        /* height: 65px; */
    }
    
    @keyframes profile-pic-change {
        0% {
            overflow: visible;
            width: 100%;
            height: unset;
            border: none;
            border-radius: 0%;
            margin: 0;
        }
        100% {
            overflow: hidden;
            width: 50px;
            height: 50px;
            border: 2px solid white;
            border-radius: 100%;
            margin: 4px;
        }
    }

    @keyframes profile-pic-image-change {
        0% {
            width: 50px;
        }
        100% {
            width: 50px;
        }
    }

.profile-info {
    position: absolute;
    width: 100%;
    bottom: 0;
    padding: 10px
}
    .profile-info.banner-mode {
        top: 0;
        bottom: auto !important;
        padding-left: 60px;
        -webkit-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
        box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
    }
